const apiKey = 'dd7abd123d47b36357eb5e8568f8526f';
let weatherUnits = 'metric';
let weatherDisplayLanguage = 'nl';

window.addEventListener('load', () => {
    let lat;
    let long;

    if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(position => {
            
            lat = position.coords.latitude;
            long = position.coords.longitude;
            console.log(lat);
            console.log(long);

            fullPosition = `${lat} + ${long}`;

            console.log(fullPosition);

            return fullPosition;

            const api = `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${long}&units=${weatherUnits}&appid=${apiKey}&lang=${weatherDisplayLanguage}`
            console.log(api);
            
        });


    } else {

    }
});